import React, { useEffect } from 'react'
import { useRecoilState, useRecoilValue } from 'recoil'
import styled from 'styled-components'
import { catogoriesSelector, IToDo, LOCAL_TODOS_KEY, toDoState } from '../atoms'

const Li = styled.li`
  padding: 10px;
`

const Span = styled.span`
  margin-right: 10px;
  font-size: 16px;
`

const Button = styled.button`
  background-color: black;
  color: white;
  margin-left: 5px;
  padding: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  &:last-child {
    background-color: red;
  }
`

function ToDo({ text, category, id }: IToDo) {
  const categories = useRecoilValue(catogoriesSelector)
  const [toDos, setToDos] = useRecoilState(toDoState)
  const onClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    const {
      currentTarget: { name },
    } = event
    setToDos((oldToDos) => {
      const targetIndex = oldToDos.findIndex((toDo) => toDo.id === id)
      const newToDo = { text, id, category: name as any }
      return [
        ...oldToDos.slice(0, targetIndex),
        newToDo,
        ...oldToDos.slice(targetIndex + 1),
      ]
    })
  }
  const onClickDelete = () => {
    setToDos((oldToDos) => {
      const targetIndex = oldToDos.findIndex((toDo) => toDo.id === id)
      return [
        ...oldToDos.slice(0, targetIndex),
        ...oldToDos.slice(targetIndex + 1),
      ]
    })
  }
  useEffect(() => {
    window.localStorage.setItem(LOCAL_TODOS_KEY, JSON.stringify(toDos))
  }, [toDos])
  return (
    <Li>
      <Span>{text}</Span>
      {categories?.map((category) => (
        <Button name={category} onClick={onClick}>
          {category}
        </Button>
      ))}
      <Button onClick={onClickDelete}>Delete</Button>
    </Li>
  )
}

export default ToDo
