import { atom, selector } from 'recoil'

export const LOCAL_TODOS_KEY = 'toDos'
export const LOCAL_CATEGORIES_KEY = 'categories'

export enum Categories {
  'TO_DO' = 'TO_DO',
  'DOING' = 'DOING',
  'DONE' = 'DONE',
}

export interface IToDo {
  text: string
  id: number
  category: Categories | string
}

export const categoriesState = atom<Categories[] | string[]>({
  key: 'categories',
  default: [Categories.TO_DO, Categories.DOING, Categories.DONE],
})

export const categoryState = atom<Categories | string>({
  key: 'category',
  default: Categories.TO_DO,
})

export const toDoState = atom<IToDo[]>({
  key: 'toDo',
  default: [],
})

export const toDoSelector = selector({
  key: 'toDoSelector',
  get: ({ get }) => {
    const toDos = get(toDoState)
    const category = get(categoryState)
    return toDos.filter((toDo) => toDo.category === category)
  },
})

export const catogoriesSelector = selector({
  key: 'categoriesSelector',
  get: ({ get }) => {
    const categories = get(categoriesState)
    const currentCategory = get(categoryState)
    return categories.filter((category) => category !== currentCategory)
  },
})
